import http from './http'

/**
 * 校验用户名是否重复注册
 */

export async function checkUsername(username) {
    return await http.request({
        url: `/username/${username}/count`,
        method: 'get'
    });
}

export async function checkMobile(mobile) {
    return await http.request({
        url: `mobiles/${mobile}/count`,
        method: 'get'
    })

}

export async function getCaptcha(uuid) {
    return await http.request({
        url: `image_codes/${uuid}`,
        method: 'get'
    })
}

export async function getVerification(data) {
    return await http.request({
        url: `sms_codes/${data.mobile}?image_code=${data.image_code}&image_code_id=${data.image_code_id}`,
    })

}

export async function registerUser(data) {
    return await http.request({
        url: 'register',
        method: 'post',
        data
    })

}
