<template>
  <div>
    <!-- :rules="rules" -->
    <div class="register">
      <div class="title">
        <div class="register-title">用户注册</div>
        <div class="login-name"><a>登录</a></div>
        <div class="login iconfont icon-denglutijiao" @click="redirect"></div>

      </div>

      <el-form
              label-position="left"
              :rules="rules"
              :model="ruleForm"
              ref="ruleForm"
              label-width="100px"
              class="demo-ruleForm"
      >
        <el-form-item label="用户名:" prop="username" :error="name_error">
          <el-input type="text" v-model="ruleForm.username" autocomplete="off" @blur="checkName"></el-input>
        </el-form-item>
        <el-form-item label="密码:" prop="password">
          <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="确认密码:" prop="check_password">
          <el-input type="password" v-model="ruleForm.check_password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号:" prop="mobile" :error="mobile_error">
          <el-input type="text" v-model="ruleForm.mobile" autocomplete="off" @blur="checkPhone"></el-input>
        </el-form-item>
        <el-form-item label="图形验证码:" prop="verification" class="verification">
          <el-input type="text" v-model="ruleForm.verification" autocomplete="off"></el-input>
          <img :src="img_src" alt="图形验证码" @click="getUuid"/>
        </el-form-item>
        <el-form-item label="短信验证码:" prop="mobile_code" class="mobile">
          <el-input type="text" v-model="ruleForm.mobile_code" autocomplete="off"></el-input>
          <el-button type="primary" @click="getSMS" :disabled="is_disable">{{ btn_msg }}</el-button>
        </el-form-item>
        <el-form-item style="left: 1px">
          <el-checkbox v-model="ruleForm.checked">同意用户协议</el-checkbox>
          <br>
          <el-button type="primary" @click="submitForm" class="register-btn" :loading="confirmBtnDis">注册</el-button>
        </el-form-item>
      </el-form>
    </div>

  </div>
</template>
<script>
import { checkUsername, checkMobile, getCaptcha, getVerification, registerUser } from '@/service/register-api'
import { megWarn, megSuc } from '@/utils/message';
export default {
  mounted() {
    this.getUuid()
  },
  data() {

    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        let re = /^.*(?=.{6,})(?=.*\d)(?=.*[A-Za-z]).*$/
        if (!re.test(value)){
          callback(new Error('密码最少6位，包括至少字母，数字'))
        }
        if (this.ruleForm.check_password !== '') {
          this.$refs.ruleForm.validateField('check_password');
        }
        callback();
      }
    };
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.ruleForm.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    let validateMobile = (rule, value, callback) => {
      let re = /^1[3-9]\d{9}$/
      if (value === ''){
        callback(new Error('手机号为必填字段'))
      } else if (!re.test(value)){
        callback(new Error('请输入11位手机号'))
      } else {
        callback()
      }
    }
    return {
      img_uuid: '',
      name_error: '',
      mobile_error: '',
      img_src: '',
      btn_msg: '获取短信验证码',
      is_disable: false,
      confirmBtnDis: false,
      ruleForm: {
        username: '',
        password: '',
        check_password: '',
        mobile: '',
        verification: '',
        mobile_code: '',
        checked: false
      },
      rules: {
        username: [
          { required: true, message: '请输入注册的用户名', trigger: 'blur' },
          { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, validator: validatePass, trigger: 'blur' }
        ],
        check_password: [
          { required: true, validator: validatePass2, trigger: 'blur' }
        ],
        mobile: [
          { required: true, validator: validateMobile, trigger: 'change' }
        ],
        verification: [
          { required: true, message: '请输入验证码', trigger: 'blur' }
        ],
        mobile_code: [
          { required: true, message: '请输入短信验证码', trigger: 'blur' },
        ]

      }
    };
  },
  methods: {
    getUuid() {
      this.img_uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        let r = Math.random() * 16 | 0,
                v = c === 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
      });
      getCaptcha(this.img_uuid)
              .then((res) => {
                this.img_src = `data:image/jpg;base64,${res.msg}`
              })
    },
    checkName() {
      console.log('aaaa')
      if (this.ruleForm.username.length >= 3 && this.ruleForm.username.length <= 20){
        checkUsername(this.ruleForm.username)
                .then(res => {
                  this.name_error = res.count > 0 ? '用户已存在' : ''
                })
      }
    },
    checkPhone(){
      let re = /^1[3-9]\d{9}$/
      if (re.test(this.ruleForm.mobile)){
        checkMobile(this.ruleForm.mobile)
                .then(res => {
                  if (res.count > 0){
                    this.mobile_error = res.errmsg
                  }
                })
      }
    },
    submitForm() {
      this.$refs.ruleForm.validate(async (valid) => {
        if (valid){
          this.confirmBtnDis = true
          if (!this.ruleForm.checked){
            megWarn('请同意协议')
            this.confirmBtnDis = false
          } else {
            registerUser(this.ruleForm)
            .then((res) => {
              if (res.code === 2000){
                megSuc('注册成功')
                this.$router.push('/login')
              }else{
                this.confirmBtnDis = false
              }
            })
          }
        } else {
          return false
        }

      });
    },
    redirect(){
      this.$router.replace('/login')
    },
    getSMS() {
      let data = {
        mobile: this.ruleForm.mobile,
        image_code: this.ruleForm.verification,
        image_code_id: this.img_uuid
      }
      if (this.ruleForm.verification && this.ruleForm.mobile){
        this.is_disable = true
        this.btn_msg = 60
        let timer = setInterval (() => {
          this.btn_msg -= 1
        }, 1000);
        setTimeout(() => {
          clearInterval(timer)
          this.is_disable = false
          this.btn_msg = '获取短信验证码'
        }, 60000)
        getVerification(data)
        .then((res) => {
          if (res.code === 400){
            this.is_disable = false
            this.btn_msg = '获取短信验证码'
            clearInterval(timer)
            if (res.errmsg === '图形验证码失效'){
              this.getUuid()
              this.ruleForm.verification = ''
            }
          } else {
            megSuc(res.msg)
          }
        })
      } else {
        if (!this.ruleForm.verification && !this.ruleForm.mobile){
          megWarn('请填写图形验证码及手机号')
        }else if (!this.ruleForm.verification){
          megWarn('请填写图形验证码')
        } else {
          megWarn('请填写手机号')
        }

      }

    }
  }
}
</script>

